  flex-direction: column;
  color: #f5f5f5;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: transparent;
  position: relative;
}
body::before {
  content: '';
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  width: 1px;
  background: linear-gradient(180deg, 
    rgba(100, 100, 100, 0.2) 0%, 
    rgba(100, 100, 100, 0.15) 50%, 
    rgba(100, 100, 100, 0.1) 100%);
  z-index: 100;
  pointer-events: none;
}
main {
  position: relative;
}
main::before,
main::after {
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  height: 2px;
  background: linear-gradient(90deg, 
    transparent 0%, 
    rgba(100, 100, 100, 0.2) 20%, 
    rgba(100, 100, 100, 0.3) 50%, 
    rgba(100, 100, 100, 0.2) 80%, 
    transparent 100%);
  z-index: 10;
  pointer-events: none;
}
main::before {
  top: 0;
}
